const Type = require('./Type');
const Value = require('./Value');
const Cont = require('./Counters');

class Logical {
    constructor(left, right, t, te, _row, _column) {
        this.type = t;
        this.node_left = left;
        this.node_right = right;
        this.type_exp = te;
        this.row = _row;
        this.column = _column;
    }

    operate(tab) {
        var cont = new Cont();
        var tempL = null;
        var tempR = null;
        /*var tf = '';
        if (cont.getTagsSize() == 0) {
            tf = cont.getNextLabel();
            //cont.pushTagsvf(tf);
        } else {
            tf = cont.getTagsvf();
            //cont.pushTagsvf(tf);
        }*/
        if (this.node_right != null) {
            tempR = this.node_right.operate(tab);
        }

        if (this.node_left != null) {
            tempL = this.node_left.operate(tab);
        }

        if (tempR != null && tempL != null) {
            if (tempL.type_exp == Type.VALOR && tempR.type_exp == Type.VALOR) {
                if (tempL.type == Type.BOOL && tempR.type == Type.BOOL) {
                    if (null != this.type) {
                        var tf = cont.getNextLabel();
                        var to = cont.getNextLabel();
                        var t1 = cont.getNextTemporal();
                        switch (this.type) {
                            case Type.AND:
                                //return new Value(tempL.value && tempR.value, Type.BOOL, Type.VALOR, this.row, this.column);
                                //return new Value(cont.generateInstruction(tempL.value, '&&', tempR.value), Type.BOOL, Type.VALOR, this.row, this.column);

                                cont.putInstruction('if(' + tempL.value + ' <> 1) goto ' + tf + ';');
                                cont.putInstruction('if(' + tempR.value + ' <> 1) goto ' + tf + ';');
                                //cont.popTagsvf()
                                /*if (cont.getTagsSize() == 0) {
                                    var to = cont.getNextLabel();
                                    cont.putInstruction(t1 + ' = 1' + ';');
                                    cont.putInstruction('goto ' + to + ';');
                                    cont.putInstruction(tf + ':');
                                    cont.putInstruction(t1 + ' = 0' + ';');
                                    cont.putInstruction(to + ':');
                                    return new Value(t1, Type.BOOL, Type.VALOR, this.row, this.column);
                                }*/
                                cont.putInstruction(t1 + ' = 1' + ';');
                                cont.putInstruction('goto ' + to + ';');
                                cont.putInstruction(tf + ':');
                                cont.putInstruction(t1 + ' = 0' + ';');
                                cont.putInstruction(to + ':');
                                return new Value(t1, Type.BOOL, Type.VALOR, this.row, this.column);
                            case Type.OR:
                                //return new Value(tempL.value || tempR.value, Type.BOOL, Type.VALOR, this.row, this.column);
                                //return new Value(cont.generateInstruction(tempL.value, '||', tempR.value), Type.BOOL, Type.VALOR, this.row, this.column);
                                cont.putInstruction('if(' + tempL.value + ' == 1) goto ' + tf + ';');
                                cont.putInstruction('if(' + tempR.value + ' == 1) goto ' + tf + ';');
                                //cont.popTagsvf()
                                /*if (cont.getTagsSize() == 0) {
                                    var to = cont.getNextLabel();
                                    cont.putInstruction(t1 + ' = 0' + ';');
                                    cont.putInstruction('goto ' + to + ';');
                                    cont.putInstruction(tf + ':');
                                    cont.putInstruction(t1 + ' = 1' + ';');
                                    cont.putInstruction(to + ':');
                                    return new Value(t1, Type.BOOL, Type.VALOR, this.row, this.column);
                                }*/
                                cont.putInstruction(t1 + ' = 0' + ';');
                                cont.putInstruction('goto ' + to + ';');
                                cont.putInstruction(tf + ':');
                                cont.putInstruction(t1 + ' = 1' + ';');
                                cont.putInstruction(to + ':');
                                return new Value(t1, Type.BOOL, Type.VALOR, this.row, this.column);
                            case Type.XOR:
                                //return new Value(!(tempL.value == tempR.value), Type.BOOL, Type.VALOR, this.row, this.column);
                                //return new Value(cont.generateInstruction(!(tempL.value, '==', tempR.value)), Type.BOOL, Type.VALOR, this.row, this.column);
                                cont.putInstruction('if(' + tempL.value + ' <> ' + tempR.value + ') goto ' + tf + ';');
                                //cont.popTagsvf()
                                /*if (cont.getTagsSize() == 0) {
                                    var to = cont.getNextLabel();
                                    cont.putInstruction(t1 + ' = 0' + ';');
                                    cont.putInstruction('goto ' + to + ';');
                                    cont.putInstruction(tf + ':');
                                    cont.putInstruction(t1 + ' = 1' + ';');
                                    cont.putInstruction(to + ':');
                                    return new Value(t1, Type.BOOL, Type.VALOR, this.row, this.column);
                                }*/
                                cont.putInstruction(t1 + ' = 0' + ';');
                                cont.putInstruction('goto ' + to + ';');
                                cont.putInstruction(tf + ':');
                                cont.putInstruction(t1 + ' = 1' + ';');
                                cont.putInstruction(to + ':');
                                return new Value(t1, Type.BOOL, Type.VALOR, this.row, this.column);
                            default:
                                break;
                        }
                    }
                    //olc2_p1.IDE.txtExec += "Error Semantico, No se puede ejecutar la operacion " + this.type.toString() + ", No reconocida o No Permitida. Linea: " + this.row + " Columna: " + this.column + "\n";
                    cont.putError(Type.SEMANTICO, "No se puede ejecutar la operacion " + this.type + ", No reconocida o No Permitida.", this.row, this.column);
                }
            }
        } else if (tempR == null && tempL != null) {
            if (tempL.type_exp == Type.VALOR) {
                if (this.type == Type.NOT) {
                    var tf = cont.getNextLabel();
                    var to = cont.getNextLabel();
                    var t1 = cont.getNextTemporal();
                    cont.putInstruction('if(' + tempL.value + ' == 1) goto ' + tf + ';');

                    cont.putInstruction(t1 + ' = 1' + ';');
                    cont.putInstruction('goto ' + to + ';');
                    cont.putInstruction(tf + ':');
                    cont.putInstruction(t1 + ' = 0' + ';');
                    cont.putInstruction(to + ':');
                    /*if (tempL.value == true) {
                        //cont.popTagsvf()
                        return new Value(0, Type.BOOL, Type.VALOR, this.row, this.column);
                    } else {
                        //cont.popTagsvf()
                        return new Value(1, Type.BOOL, Type.VALOR, this.row, this.column);
                    }*/
                    return new Value(t1, Type.BOOL, Type.VALOR, this.row, this.column);
                }
            }
            //cont.popTagsvf()
            //olc2_p1.IDE.txtExec += "Error Semantico, No se puede ejecutar la operacion " + type.toString() + ", No reconocida o No Permitida. Linea: " + row + " Columna: " + column + "\n";
            cont.putError(Type.SEMANTICO, "No se puede ejecutar la operacion " + this.type + ", No reconocida o No Permitida.", this.row, this.column);
        }
        return null;
    }

}

module.exports = Logical;