const count = require('./Counters');
const Symbol = require('./Symbol');
const Value = require('./Value');
const SymbolTable = require('./SymbolTable');
const Type = require('./Type');


class Declaration {
    constructor(_id, _value, _type, _type_exp, _type_var, _type_c, _type_o, _ambit, _row, _column) {
        this.id = _id;
        this.value = _value;
        this.type = _type;
        this.type_exp = _type_exp;
        this.type_var = _type_var;
        this.type_c = _type_c;
        this.type_o = _type_o;
        this.ambit = _ambit;
        this.row = _row;
        this.column = _column;
    }

    operate(tab) {
        const cont = new this.count();


        var tmpExp = null;

        if (this.value != null) {
            this.count.putInstruction('##Operando la expresion para la Declaracion');

            if (!Array.isArray(this.value)) {//} instanceof Value){
                tmpExp = this.value.operate(tab);
            } else {
                tmpExp = this.value[0].operate(tab);         ///////////////////aca se debe modificar para la recursion de llamadas
            }
            if (tmpExp == null) {
                cont.putError(Type.SEMANTICO, "Hubo un error al realizar la declaracion de la variable " + this.id + ".", this.row, this.column);
                return null;
            }
            if (this.checkType(tmpExp, cont)) {
                return null;
            }
        }
        for (var i = 0; i < this.id.length; i++) {
            var a = false;
            if (this.type_var == Type.GLOBAL) {
                a = tab.exists(this.id[i]);
            } else {
                a = tab.existsDirect(this.id[i]);
            }
            this.count.putInstruction('##Declarando la variable ' + this.id[i]);
            if (tmpExp == null) {
                switch (this.type) {
                    case Type.DECIMAL:
                        tmpExp = new Value(null, 0.0, this.type, this.type_exp, this.type_var, this.ambit, this.row, this.column);
                        break;
                    case Type.ENTERO:
                        tmpExp = new Value(null, 0, this.type, this.type_exp, this.type_var, this.ambit, this.row, this.column);
                        break;
                    case Type.CHAR:
                        tmpExp = new Value(null, '\0', this.type, this.type_exp, this.type_var, this.ambit, this.row, this.column);
                        break;
                    case Type.BOOL:
                        tmpExp = new Value(null, false, this.type, this.type_exp, this.type_var, this.ambit, this.row, this.column);
                        break;
                }
            }
            if (a == false) {
                var r = cont.getRelativePlus();
                var tag = cont.generateDeclaration(this.type_var, tmpExp.value, r)
                tab.addSymbolDirect(new Symbol(this.ambit, tmpExp.type, tmpExp.type_exp, this.type_var, this.type_c, this.type_o, this.id[i], r, tag));
                
                this.count.putSymbol(this.ambit, tmpExp.type, tmpExp.type_exp, this.type_var, this.type_c, this.type_o, this.id[i], r, tag);
            } else {
                cont.putError(Type.SEMANTICO, "La variable " + this.id[i] + " ya fue declarada en este ambito.", this.row, this.column);
            }

        }
        //console.log(tab);
        //olc2_p1.IDE.txtExec += "Error Semantico, Hubo un error al realizar la asignacion de la variable \"" + id + "\". Linea: " + row + " Columna: " + column + "\n";
        //cont.putError(Type.SEMANTICO, "Hubo un error al realizar la asignacion de la variable " + this.id + ".", this.row, this.column);
        return null;
    }

    checkType(val, cont) {
        switch (this.type) {
            case Type.ENTERO:
                if (val.type == Type.ENTERO) {
                    return false;
                } else if (val.type == Type.CARACTER) {
                    val.type = Type.ENTERO;
                    return false;
                } else {
                    cont.putError(Type.SEMANTICO, 'Tipo de la EXPRESION ' + val.type + ' No Asignable a un ENTERO.', this.row, this.column);
                    return true;
                }
                break;
            case Type.DECIMAL:
                if (val.type == Type.DECIMAL) {
                    return false;
                } else if (val.type == Type.ENTERO) {
                    val.type = Type.DECIMAL;
                    return false;
                } else if (val.type == Type.CARACTER) {
                    val.type = Type.DECIMAL;
                    return false;
                } else {
                    cont.putError(Type.SEMANTICO, 'Tipo de la EXPRESION ' + val.typ + ' No Asignable a un DOUBLE.', this.row, this.column);
                    return true;
                }
                break;
            case Type.CARACTER:
                if (val.type != Type.CARACTER) {
                    cont.putError(Type.SEMANTICO, 'Tipo de la EXPRESION ' + val.type + ' No Asignable a un CARACTER.', this.row, this.column);
                    return true;
                }
        }
        return false;
    }
}

module.exports = Declaration;