const Type = require('./Type');
const SymbolTable = require('./SymbolTable');
const count = require('./Counters');


class Unary{
    constructor(_id, _type, _row, _col){
        this.id = _id;
        this.type = _type;
        this.row = _row;
        this.column = _col;
    }

    operate(tab){
        this.count = new count();
        var a = tab.getSymbol(this.id);
        if(a == null){
            //error
            return null;
        }
        var index =this.count.generateInstruction('P','+',a.pointer);
        var tag =this.count.getNextTemporal();
        if(a.type_ex == Type.GLOBAL){
           this.count.putInstruction(tag+' = heap['+index+'];')
        }else{
           this.count.putInstruction(tag+' = stack['+index+'];')
        }
        if(this.type == Type.INCREMENTO){
           this.count.putInstruction(tag+' = '+tag+' + 1;')
        }else{
           this.count.putInstruction(tag+' = '+tag+' - 1;')
        }
        
        if(a.type_ex == Type.GLOBAL){
           this.count.putInstruction('heap['+index+'] = '+tag+';')
        }else{
           this.count.putInstruction('stack['+index+'] = '+tag+';')
        }
        
        return null;
    }
}


module.exports = Unary;