const Type = require('./Type');
const Count = require('./Counters');

class Assignment {
    constructor(_id, val, _row, _column) {
        this.value = val;
        this.id = _id;
        this.row = _row;
        this.column = _column;
        //this.type = t;
    }

    operate(tab) {
        var count = new Count();
        var a = tab.getSymbol(this.id[0].value);
        var tmpExp;
        count.putInstruction('##Iniciando la Asignacion de ' + this.id[0].value);
        if (!(Array.isArray(this.value))) {
            tmpExp = this.value.operate(tab);
        } else {
            tmpExp = this.value[0].operate(tab);
        }
        if (tmpExp != null) {
            if (a == null) {
                //tab.addSymbol(new Symboll(0, tmpExp.type, tmpExp.type_exp, id, tempLL.clone()));
                count.putError(Type.SEMANTICO, "Variable " + this.id[0] + " no encontrada.", this.row, this.column);
            } else {
                if (a.type_c == Type.CONST) {
                    count.putError(Type.SEMANTICO, 'No se puede asignar VALOR a ' + a.id + ' por que es de tipo CONSTANTE.', this.row, this.column);
                    return null;
                }
                if (this.checkType(tmpExp, a.type, count)) {
                    return null;
                }
                a.type_exp = tmpExp.type_exp;
                a.type = tmpExp.type;
                var t = count.getNextTemporal();
                if (a.type_var == Type.GLOBAL) {
                    //count.putInstruction(t + ' = H + ' + a.pointer + ';');
                    count.putInstruction('heap[' + a.tag + '] = ' + tmpExp.value + ';');
                } else {
                    count.putInstruction(t + ' = P + ' + a.pointer + ';');
                    count.putInstruction('stack[' + t + '] = ' + tmpExp.value + ';');
                }

            }
        } else {
            //olc2_p1.IDE.txtExec += "Error Semantico, Hubo un error al realizar la asignacion de la variable \"" + id + "\". Linea: " + row + " Columna: " + column + "\n";
            count.putError(Type.SEMANTICO, "Hubo un error al realizar la asignacion de la variable " + this.id[0] + ".", this.row, this.column);
        }
        return null;
    }

    checkType(val, type, cont) {
        switch (type) {
            case Type.ENTERO:
                if (val.type == Type.ENTERO) {
                    return false;
                } else if (val.type == Type.CARACTER) {
                    val.type = Type.ENTERO;
                    return false;
                } else {
                    cont.putError(Type.SEMANTICO, 'Tipo de la EXPRESION ' + val.type + ' No Asignable a un ENTERO.', this.row, this.column);
                    return true;
                }
                break;
            case Type.DECIMAL:
                if (val.type == Type.DECIMAL) {
                    return false;
                } else if (val.type == Type.ENTERO) {
                    val.type = Type.DECIMAL;
                    return false;
                } else if (val.type == Type.CARACTER) {
                    val.type = Type.DECIMAL;
                    return false;
                } else {
                    cont.putError(Type.SEMANTICO, 'Tipo de la EXPRESION ' + val.typ + ' No Asignable a un DOUBLE.', this.row, this.column);
                    return true;
                }
                break;
            case Type.CARACTER:
                if (val.type != Type.CARACTER) {
                    cont.putError(Type.SEMANTICO, 'Tipo de la EXPRESION ' + val.type + ' No Asignable a un CARACTER.', this.row, this.column);
                    return true;
                }
        }
        return false;
    }

}

module.exports = Assignment;
