const Count = require('./Counters');
const Type = require('./Type');

class Continue{

    constructor(_type_exp, _row, _column) {
        this.type_exp = _type_exp;
        this.row = _row;
        this.column = _column;
    }

    operate(tab) {
        var count = new Count();
        if(count.getLengthInit() > 0){
            count.putInstruction('goto '+count.getLastInit()+';');
        }else{
            count.putError(Type.SEMANTICO,'La instruccion CONTINUE solo puede estar dentro de While,For,DoWhile.',this.row,this.column);
        }
    }
}

module.exports = Continue;