const Type = require('./Type');
const SymbolTable = require('./SymbolTable');
const Count = require('./Counters');

class For{
    constructor(_decla, _exp,_assig, _body, _row, _col) {
        this.declaration = _decla;
        this.exp = _exp;
        this.assignment = _assig
        this.body = _body;
        this.row = _row;
        this.column = _col;
    }

    operate(tab) {
        var count = new Count();
        var s = new SymbolTable(tab);
        count.putInstruction('##Generando el For');
        if(this.declaration != null){
            this.declaration.operate(s);
        }
        
        
        var tagin = count.getNextLabel();
        count.putInstruction(tagin+':');
        
        var r = this.exp.operate(s);
        if (r == null) {
            count.putError(Type.SINTACTICO, "No se puede ejecutar la operacion " + r.type + ", se necesita una condicion logica o relacional.", this.row, this.column);
            return null;
        }
        if (r.type != Type.BOOL) {
            //olc2_p1.IDE.txtExec += "Error Sintactico, No se puede ejecutar la operacion " + aux.type.toString() + ", se necesita una condicion logica o relacional. Linea: " + row + " Columna: " + column + "\n";
            count.putError(Type.SINTACTICO, "No se puede ejecutar la operacion " + r.type + ", se necesita una condicion logica o relacional.", this.row, this.column);
            return null;
        }
        var e = count.generateIf(r.value,'==',0);
        count.pushFinal(e);
        var c = count.getNextLabel();
        count.pushInit(c);
        for(var i = 0; i<this.body.length; i++){
            this.body[i].operate(s)
        }
        
        if(this.assignment != null){
            count.putInstruction(c+':');
            console.log(this.assignment);
            this.assignment.operate(s);
        }
        
        count.putInstruction('goto '+tagin+';');
        count.putInstruction(e+':');
        count.popInit();
        count.popFinal();
        return null;
    }
}

module.exports = For;