const Count = require('./Counters');
const Type = require('./Type');

class Break{

    constructor(_type_exp, _row, _column) {
        this.type_exp = _type_exp;
        this.row = _row;
        this.column = _column;
    }

    operate(tab) {
        var count = new Count();
        if(count.getLengthFinal() > 0){
            count.putInstruction('goto '+count.getLastFinal()+';');
            return;
        }else{
            count.putError(Type.SEMANTICO,'La instruccion BREAK solo puede estar dentro de While,For,DoWhile,Switch.',this.row,this.column);
        }
    }
}

module.exports = Break;