const Type = require('./Type');
const SymbolTable = require('./SymbolTable');
const Count = require('./Counters');


class IfList {
	constructor() {
		this.lif = [];
		this.elsebody = [];
	}

	operate(tab) {
		var count = new Count();

		var out = count.getNextLabel();
		for (var j = 0; j < this.lif.length; j++) {
			var r = this.lif[j].exp.operate(tab);
			if (r == null) {
				count.putError(Type.SINTACTICO, "No se puede ejecutar la instruccion If, se necesita una condicion logica o relacional.", this.lif[j].row, this.lif[j].column);
				return null;
			}
			if (r.type != Type.BOOL) {
				//olc2_p1.IDE.txtExec += "Error Sintactico, No se puede ejecutar la operacion " + aux.type.toString() + ", se necesita una condicion logica o relacional. Linea: " + row + " Columna: " + column + "\n";
				count.putError(Type.SINTACTICO, "No se puede ejecutar la operacion " + r.type + ", se necesita una condicion logica o relacional.", this.lif[j].row, this.lif[j].column);
				//System.out.println("Error No se encontro una condicion logica");
			}
			var aux = count.generateIf(r.value, '==', 0);
			var s = new SymbolTable(tab);
			for (var i = 0; i < this.lif[j].body.length; i++) {
				this.lif[j].body[i].operate(s);
			}

			count.putInstruction('goto ' + out + ';');
			count.putInstruction(aux + ':');


		}
		if (this.elsebody != null) {
			//elsebody.used = used;
			//elsebody.execute(tab);
			var body = this.elsebody.body;
			var s = new SymbolTable(tab);
			for (var i = 0; i < body.length; i++) {

				body[i].operate(s);

			}
		}
		count.putInstruction(out + ':');
		/*for(var i = 0; i<outs.length; i++){
			count.putInstruction(outs[i] + ':');
		}*/
		return null;
	}

}

module.exports = IfList;